.profile__info {
  display: grid;
  grid-template-columns: 1fr 24px;
  gap: 3px 18px;
  align-content: center;
  margin-left: 30px;
}
@media screen and (max-width: 1023px) {
  .profile__info {
    gap: 4px 10px;
    margin-left: 28px;
    margin-top: 26px;
  }
}

@media screen and (max-width: 767px) {
  .profile__info {
    gap: 4px 10px;
    margin-left: 0px;
    margin-top: 26px;
  }
}
