.form__input {
  border: none;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  margin: 0;
  padding: 0 0 9px;
  font-size: 14px;
  line-height: 17px;
}

.form__input:nth-last-child(2) {
  margin: 0 0;
}

.form__input:focus {
  outline-color: rgba(15, 158, 230);
}
