.profile__button {
  padding: 0 0;
  cursor: pointer;
  background-position: center;
  background-repeat: no-repeat;
  box-sizing: border-box;
  background-color: transparent;
  border: 1px solid rgba(255, 255, 255, 1);
  transition: 0.6s ease;
}

.profile__button:hover {
  opacity: 0.6;
}
